import React, { useEffect, useState } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import useJwt from '@src/auth/jwt/useJwt'
import { Error, Success, ErrorMessage } from '../../viewhelper'
import { useHistory } from 'react-router-dom'
import CommonDataTable from '../../VendorComponents/ClientSideDataTable'

const List = ({ TableDataLoading, UserList }) => {

    const column = [
        {
            name: 'Username',
            minWidth: '100px',
            sortable: true,
            selector: 'username'
        },
        {
            name: 'Fullname',
            minWidth: '100px',
            sortable: true,
            selector: 'fullname'
        },
        {
            name: 'Email',
            minWidth: '100px',
            sortable: true,
            selector: 'emailid'
        },
        {
            name: 'Status',
            minWidth: '100px',
            // sortable: true,
            selector: row => {
                const statusBG = {
                    0: { title: 'Inactive', color: 'light-danger' },
                    1: { title: 'Active', color: 'light-success' }
                }
                return <Badge color={statusBG[row.userstatus].color} pill className='px-1'>
                    {statusBG[row.userstatus].title}
                </Badge>
            }
        },
        {
            name: 'Action',
            minWidth: '100px',
            // sortable: true,
            selector: row => {
                return <>
                    {/* <span title="Edit">
                        <Edit size={15}
                            color='teal'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => {
                                setsubUserInfo(row)
                                setModal(true)
                            }}
                        />
                    </span>&nbsp;&nbsp;
                    <span title="Delete">
                        <Trash size={15}
                            color='red'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => handlePoPupActions(row.mobileno, 'Do you want to delete?')}
                        />
                    </span> */}
                </>
            }
        }
    ]
    return (
        <Card>
            <Row>
                <Col md='12'>
                    <CommonDataTable column={column} TableData={UserList} TableDataLoading={TableDataLoading} />
                </Col>
            </Row>
        </Card>
    )
}

export default List