import React, { useEffect, useState } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, CheckCircle
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import useJwt from '@src/auth/jwt/useJwt'
import { Error, Success, ErrorMessage } from '../../viewhelper'
import { useHistory } from 'react-router-dom'
import CommonDataTable from '../../VendorComponents/ClientSideDataTable'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)


const Approve = ({ TableDataLoading, UserList, refresh, setRefresh }) => {

    const handlePoPupActions = (username, action_id, message) => {
        return MySwal.fire({
            title: message,
            text: `You won't be able to revert this`,
            icon: 'warning',
            allowOutsideClick: false,
            allowEscapeKey: false,
            showCancelButton: true,
            confirmButtonText: 'Yes',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-danger ml-1'
            },
            showLoaderOnConfirm: true,
            preConfirm: () => {
                // return true
                return useJwt.approveRejectAdminUsers({ username, action_id }).then(res => {
                    Success(res)
                    console.log(res)
                    setRefresh(!refresh)
                }).catch(err => {
                    console.log(err.response)
                    Error(err)
                })
            },
            buttonsStyling: false,
            allowOutsideClick: () => !Swal.isLoading()
        }).then(function (result) {
            if (result.isConfirmed) {

            }
        })

    }
    const column = [
        {
            name: 'Username',
            minWidth: '100px',
            sortable: true,
            selector: 'username'
        },
        {
            name: 'Fullname',
            minWidth: '100px',
            sortable: true,
            selector: 'fullname'
        },
        {
            name: 'Email',
            minWidth: '100px',
            sortable: true,
            selector: 'emailid'
        },
        {
            name: 'Status',
            minWidth: '100px',
            // sortable: true,
            selector: row => {
                const statusBG = {
                    0: { title: 'Inactive', color: 'light-danger' },
                    1: { title: 'Active', color: 'light-success' }
                }
                return <Badge color={statusBG[row.userstatus].color} pill className='px-1'>
                    {statusBG[row.userstatus].title}
                </Badge>
            }
        },
        {
            name: 'Action',
            minWidth: '100px',
            // sortable: true,
            selector: row => {
                return <>
                    <span title="Approve">
                        <CheckCircle size={15}
                            color='dodgerblue'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => handlePoPupActions(row.username, 1, 'Do you want to approve?')}
                        />
                    </span>&nbsp;&nbsp;
                    <span title="Delete">
                        <Trash size={15}
                            color='red'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => handlePoPupActions(row.username, 2, 'Do you want to delete?')}
                        />
                    </span>
                </>
            }
        }
    ]
    return (
        <Card>
            <Row>
                <Col md='12'>
                    <CommonDataTable column={column} TableData={UserList} TableDataLoading={TableDataLoading} />
                </Col>
            </Row>
        </Card>
    )
}

export default Approve